import Vue from 'vue'
import Router from 'vue-router'
const Index = () => import('@/components/Index')
const MainContent = () => import('@/components/MainContent')
const ChangeBankCard = () => import('@/components/ChangeBankCard')
const ChangeBankPhone = () => import('@/components/ChangeBankPhone')
const OpenAcc = () => import('@/components/OpenAcc')
const ChangeStatus = () => import('@/components/ChangeStatus')
const Login = () => import('@/components/Login')
Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior () {
    return {x: 0, y: 0}
  },
  routes: [
    {
      path: '/',
      component: Index,
      children: [
        {
          path: '',
          name: 'main',
          component: MainContent
        }, {
          path: 'changebankcard',
          name: 'changebankcard',
          component: ChangeBankCard
        }, {
          path: 'changebankphone',
          name: 'changebankphone',
          component: ChangeBankPhone
        }, {
          path: 'openacc',
          name: 'openacc',
          component: OpenAcc
        }, {
          path: 'changestatus',
          name: 'changestatus',
          component: ChangeStatus
        }
      ]
    }, {
      path: '/login',
      component: Login
    }

  ]
})



